<template>
    <div class="tin-property">
        <el-select v-model="type" placeholder="请选择三角网类型" @change="selectTinType">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
        </el-select>
    </div>

</template>

<script>
    export default {
        data() {
            return {
                type: "",
                options: [{
                    label: "地球椭球面",
                    value: "ellipsoid"
                }, {
                    label: "等角圆柱投影",
                    value: "equalAngle"
                }, {
                    label: "等距离圆柱投影",
                    value: "equalDis"
                }, {
                    label: "等面积圆柱投影",
                    value: "equalArea"
                }],
            }
        },
        methods: {
            selectTinType() {
                if (this.type == "ellipsoid") this.addGeoJSON("ellipsoid.json", "#f00");
                if (this.type == "equalAngle") this.addGeoJSON("equalAngle.json", "#0f0");
                if (this.type == "equalDis") this.addGeoJSON("equalDis.json", "#00f");
                if (this.type == "equalArea") this.addGeoJSON("equalArea.json", "#ff0");
            },
            addGeoJSON(fileName, color) {
                viewer.dataSources.removeAll();

                viewer.dataSources.add(Cesium.GeoJsonDataSource.load(`./tinData/${fileName}`, {
                    stroke: Cesium.Color.fromCssColorString(color),
                    fill: Cesium.Color.fromCssColorString(color),
                    strokeWidth: 1
                }));
            }
        }

    }
</script>
<style>
    .tin-property {
        position: fixed;
        left: 0px;
        top: 0px;
        display: flex;
        flex-direction: column;
    }
</style>